@use "global" as *;

.pc-info {
  @include mq("md") {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  }
}

.pc-info__text {
  flex-grow: 1;
}

.pc-info__guide {
  color: $green-accent;
  text-align: center;
    margin-top: rem(32);
    margin-bottom: 0;
// height: 100%;
}

.pc-info__guide p:first-child {
  font-family: $third-font-family;    letter-spacing: .03em;

}

.pc-info__guide p:nth-child(2) {
font-size: rem(14);
}


.pc-info__btn {
    margin-top: rem(15);
}