@use "global" as *;

.privacy {
  line-height: 2;
    // padding-bottom: rem(32);
}

.privacy__inner {
  @include mq("md") {
    max-width: rem(850);
  }
}

.privacy__title {
  font-weight: $medium;
  @include mq("md") {
    font-size: rem(32);
  }
}

.privacy__top-text {
  margin-top: rem(40);
}

.privacy__content {
  margin-top: rem(35);
  @include mq("md") {
    margin-top: rem(30);
  }
}

.privacy__content p:first-child {
  margin-bottom: rem(35);
}

.privacy__content ol {
  padding-left: rem(24);
  list-style-type: decimal;
}

.privacy__content p {
  margin-top: rem(32);
}

.privacy__bottom-text {
  margin-top: rem(30);
}

