@use "global" as *;

.pagination {
  margin-top: rem(80);
}

.pagination__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  padding: 0;
  gap: rem(12);
  @include mq("md") {
    gap: rem(80);
  }
}

.pagination a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 2.375em;
  height: 2.375em;
  border: 1px solid $green-accent;
  border-radius: 1px;
  color: $green-accent;
  background: $white;
  font-family: $third-font-family;
  font-weight: $bold;
  transition: .3s all;
}

.pagination a:hover {
  background: $green-accent;
  color: $white;
}

