@use "global" as *;

// 下層　キャンペーン
.tab {
  display: grid;
  gap: rem(16) rem(22);
  grid-template-columns: 1fr 1fr;
  @include mq("md") {
    gap: rem(32);
    display: flex;
  }
}

.tab__item {
  // // cursor: pointer;
  position: relative;
  z-index: 1;
  width: 100%;
  max-width: rem(162);

  
  border: 1px solid $green-accent;
  background: $white;
  color: $green-accent;
}

.tab__item.active {
  background: $green-accent;
  color: $white;
}

.tab__item:hover {
  background: $green-accent;
  color: $white;
}

.tab__item a {
  display: inline-block;
  width: 100%;
  padding: rem(5) 0;
  font-size: rem(14);
  transition: 0.3s all;
  text-align: center;
}

.tab__sub-campaign {
  display: grid;
  gap: rem(40);
  grid-template-columns: 1fr;
  @include mq("md") {
    gap: rem(80) rem(40);
    grid-template-columns: 1fr 1fr;
  }
}

.tab__sub-page-inner {
  padding-left: rem(33);
  padding-right: rem(33);
  @include mq("md") {
  padding-left: 0;
  padding-right: 0;
  }
}
