@use "global" as *;


.pagenavi {
  margin-top: rem(80);
}

.wp-pagenavi {
  display: flex;
  justify-content: center;
  gap: rem(12);
}

.wp-pagenavi a, .wp-pagenavi span {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 2.375em;
  height: 2.375em;
  border: 1px solid $green-accent;
  border-radius: 1px;
  color: $green-accent;
  background: $white;
  font-family: $third-font-family;
  font-weight: $bold;
  transition: .3s all;
}

.wp-pagenavi a:hover{
  background: $green-accent;
  color: $white;
  opacity: 1;
}

.wp-pagenavi span.current{
  background: $green-accent;
  color: $white;
}